<template>
  <div class="after-pay">
    <!-- <h1>{{ msg }}</h1> -->
    <img v-if="title == '付款成功！'" src="../assets/img/zhifuchenggong@3x.png" alt="支付成功">
    <img v-else src="../assets/img/zhuyishixiang@3x.png" alt="支付失败">
    <h1>{{title}}</h1>
    <div class="btn-group">
      <router-link :to="{name:'Index', params:{tab: 'tab-order'}}">
        <mt-button size="normal" class="btn-default out-line">查看订单 </mt-button>
      </router-link>
      <mt-button size="normal" v-if="title == '付款成功！'" class="btn-default full" @click="share()">分享朋友圈</mt-button>
      <mt-button size="normal" v-else class="btn-default full" @click="pay()">重新支付</mt-button>
    </div>
    <mt-popup ref="popup"
      v-model="popupVisible"
      position="top"
      >
      <div class="popup-box" @click="close()">
        <img src="../assets/img/sharebg-s.png" alt="">
      </div>
    </mt-popup>
  </div>
</template>
<script>
import axios from '@/utils/axios'
import { wxPay, getOrderInfo, wxShare } from '@/utils/auth'
import { Toast } from 'mint-ui'
import router from '@/router'
// import { setTimeout } from 'timers'
export default {
  name: 'AdterPay',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      title: '',
      result: {
        title: ''
      },
      orderInfo: '',
      popupVisible: false
    }
  },
  created () {
    this.resetTitle('付款结果')
    this.orderInfo = JSON.parse(getOrderInfo())
    if (!this.orderInfo.orderId) {
      Toast('订单信息丢失')
      router.push({name: 'Index'})
    }
    if (parseInt(this.$route.params.result) === 1) {
      this.title = '付款成功！'
      this.setShareInfo()
    } else {
      this.title = '付款失败！'
    }
  },
  methods: {
    setShareInfo () {
      // 分享配置
      axios.post('/api/activity/getShareinfo', {orderId: this.orderInfo.orderId}).then(res => {
        wxShare({
          title: res.title,
          link: `${process.env.SHARE_ORIGIN}/#/video/${res.id}`,
          imgUrl: res.headimg,
          desc: `${res.nickname}也参加了，你不来吗？`
        }, () => {
          this.close()
        })
      })
    },
    pay () {
      axios.postJson('/api/payment/prepay', {orderId: this.orderInfo.orderId}).then(res => {
        wxPay(res, this.orderInfo.orderId).then(result => {
          // 支付成功
          this.title = '付款成功！'
          this.this.setShareInfo()
        }).catch(result => {
          // 支付失败
          this.title = '付款失败！'
        })
      })
    },
    share () {
      // Toast('请点击右上角分享')
      this.popupVisible = true
      // setTimeout(() => {
      //   document.getElementsByClassName('v-modal')[0].style.backgroundColor = '#17181a'
      //   document.getElementsByClassName('v-modal')[0].style.opacity = 1
      // })
    },
    close () {
      this.popupVisible = false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.after-pay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #fff;
  text-align: center;
  img {
    margin-top: 1.3rem;
    // margin-left:
    width: 1.6rem;
  }
  h1{
    font-size: .56rem;
  }
  .btn-group button{
      margin: 0 .3rem;
      width: 2.4rem;
      height: .7rem;
  }
  .mint-popup {
    background: transparent;
  }
  .popup-box {
    width: 7.5rem;
    height: 100%;
    img{
      display: block;
      width: 100%;
      margin-top: 0;
    }
  }
}

</style>



