import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Index from '@/components/Index'
import Video from '@/components/Video'
import Course from '@/components/Course'
import Order from '@/components/Order'
import AdterPay from '@/components/AfterPay'

// import axios from '@/utils/axios'
import { getToken, setToken, getAllUrlParam, wxConfig, getConfig,
  wxHideMenu, wxShowMenu, setShopId } from '@/utils/auth'

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    }, {
      path: '/index/:tab',
      name: 'Index',
      component: Index
    }, {
      path: '/clear',
      name: 'Clear',
      component: HelloWorld
    }, {
      path: '/video/:id',
      name: 'Video',
      component: Video
    }, {
      path: '/course/:id',
      name: 'Course',
      component: Course
    }, {
      path: '/order/:activeId',
      name: 'Order',
      component: Order
    }, {
      path: '/afterpay/:result',
      name: 'AdterPay',
      component: AdterPay
    }
  ]
})

router.beforeEach((to, from, next) => {
  let urlParams = getAllUrlParam()
  let token = urlParams.token
  // let code = urlParams.code
  if (process.env.SERVER_ENV === 'php' && urlParams.shopid) {
    setShopId(urlParams.shopid)
  }

  console.log('==当前服务器环境==')
  console.log(process.env.SERVER_ENV)
  if (!token && !getToken()) {
    // if (!code) {
    console.log('=====微信跳转取token=====')
    let loginUrl = `${process.env.BASE_API}/api/wechat/getToken?jump=${process.env.SHARE_ORIGIN}/|${to.fullPath}`
    if (urlParams.shopid) {
      loginUrl += `&shopid=${urlParams.shopid}`
    }
    console.log(loginUrl)
    window.location.href = loginUrl
    return false
    // } else if (process.env.SERVER_ENV === 'java' && code) {
    //   console.log('=====用openid取token=====')
    //   axios.get(`/api/oauth/token`, {params: {openid: code}}).then(res => {
    //     setToken(res)
    //     window.location.href = `${window.location.origin}${window.location.pathname}#${to.fullPath}`
    //   })
    // }
  } else {
    // 如果url 带参数，把参数清掉
    if (token) {
      console.log('如果url 带参数，把参数清掉')
      setToken(token)
      if (urlParams.shopid) {
        setShopId(urlParams.shopid)
      }
      window.location.href = `${window.location.origin}${window.location.pathname}${window.location.hash}` // window.location.origin + window.location.pathname + window.location.hash
      return false
    }
    if (window.location.href.indexOf('?') !== -1) {
      window.location.href = `${process.env.SHARE_ORIGIN}/#${to.fullPath}`
      return false
    }
    if (!getConfig()) {
      wxConfig() // .then(result => next()) // 微信sdk初始化
    }
    if (to.name === 'Index' || to.name === 'Video' || to.fullPath === '/afterpay/1') {
      // 显示微信菜单
      wxShowMenu()
    } else {
      // 关闭微信菜单
      wxHideMenu()
    }
    // if (process.env.SERVER_ENV === 'php') {
    // console.log('判断有没有config过')
    // }
    next()
  }
})
export default router



