import axios from 'axios'
import { getToken } from '@/utils/auth'
// import bus from '@/utils/bus'
import { Toast } from 'mint-ui'

// 创建axios实例
const service = axios.create({
  baseURL: process.env.BASE_API, // api的base_url
  // baseURL: base_url,
  timeout: 5000, // 请求超时时间
  transformRequest: function (obj) {
    let ret = ''
    for (let it in obj) {
      ret += encodeURIComponent(it) + '=' + encodeURIComponent(obj[it]) + '&'
    }
    return ret
  },
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded'
  }
})

// request拦截器
service.interceptors.request.use(
  config => {
    if (config.method === 'post') {
      config.data = {
        ...config.data,
        _t: Date.parse(new Date()) / 1000
      }
    } else if (config.method === 'get') {
      config.params = {
        _t: Date.parse(new Date()) / 1000,
        ...config.params
      }
    }
    if (process.env.SERVER_ENV === 'java' && getToken()) {
      config.headers['Authorization'] = getToken()
    } else if (process.env.SERVER_ENV === 'php') {
      if (config.method === 'post') {
        config.data.token = getToken()
      } else if (config.method === 'get') {
        config.params.token = getToken()
      }
    }
    return config
  },
  error => {
    console.log(error) // for debug
    Promise.reject(error)
  }
)

// respone拦截器
service.interceptors.response.use(
  response => {
    let error
    if (response.status === 200) {
      if (process.env.SERVER_ENV === 'php') {
        if (response.data.code === 1) {
          return response.data.data
        } else {
          error = new Error(response.data.message)
          error.status = response.data.code
          Toast(response.data.message)
        }
      } else if (process.env.SERVER_ENV === 'java') {
        if (response.data.code === 200) {
          return response.data.data
        } else {
          error = new Error(response.data.message)
          error.status = response.data.code
          Toast(response.data.message)
        }
      }
    } else {
      error = new Error(response.data)
      Toast(response.data)
    }
    return Promise.reject(error)
  },
  error => {
    console.log('err' + error) // for debug
    return Promise.reject(error)
  }
)

service.postJson = (path, params) => {
  if (process.env.SERVER_ENV === 'php') {
    params.token = getToken()
  }
  return service({
    url: path,
    method: 'post',
    data: params,
    transformRequest: function (obj) {
      // obj.token = getToken()
      return JSON.stringify(obj)
    },
    headers: {
      'Content-Type': 'application/json'
      // 'X-Auth-Token': getToken()
    }
  })
}

service.postForm = (path, params) => {
  if (process.env.SERVER_ENV === 'php') {
    params.token = getToken()
  }
  return service({
    url: path,
    method: 'post',
    data: params
    // headers: {
    //   'X-Auth-Token': getToken()
    // }
  })
}

service.requestAll = taskArr => {
  return Promise.all(taskArr)
}

export default service



