<template>
  <div class="course">
    <mt-header fixed :title="`从${formcity}出发`"></mt-header>
    <div class="itinerary">
      <div class="item" v-for="item in active" :key="item.title">
        <img class="icon" v-if="item.icon" :src="'../assets/img/'+item.icon+'@3x.png'" alt="icon">
        <img class="icon" v-else  src="../assets/img/daba@3x.png" alt="icon">
        <time class="time">{{item.date | date}}</time>
        <div class="detail">
          <div class="title">
            {{item.title}}
          </div>
          <div class="info" v-html="item.intro">
            <!-- 参考航班Ho1381（09:15-14:00）抵达机场后导游接机入住酒店交通：飞机/巴士接机 餐食 xxx酒店：留寿都度假村 -->
          </div>
        </div>
      </div>
    </div>

    <mt-popup ref="popup"
      v-model="popupVisible"
      position="bottom"
      >
      <div class="popup-box">
        <div class="title">预定席位</div>
        <div class="format-box" v-for="item in format" :key="item.name">
          <div class="format">
            <div class="name">
              <div class="line-clamp">{{item.name}}</div>
              <span class="rmb">¥ {{item.price | money}}</span>
            </div>
            <div class="info">
              {{item.intro}}
            </div>
            <div class="number">
              <span @click="count(item,1)">+</span>
              <span>{{item.number}}</span>
              <span @click="count(item,-1)">-</span>
            </div>
          </div>
        </div>
        <div class="price" ref="price">
          <div class="right" @click="order()">
            马上预定
          </div>
          <div class="left">
            合计 <span class="rmb">¥ {{price | money}} </span>
          </div>
        </div>
      </div>
    </mt-popup>

    <div class="price" ref="price">
      <div class="right" @click="order()">
        马上预定
      </div>
      <div class="left">
        合计 <span class="rmb">¥ {{price | money}} </span>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '@/utils/axios'
import { setOrderInfo } from '@/utils/auth'
// import router from '@/router'
import { Toast } from 'mint-ui'
export default {
  name: 'Course',
  data () {
    return {
      msg: '行程安排',
      active: [],
      format: [],
      popupVisible: false,
      price: '0',
      formcity: ''
    }
  },
  created () {
    // 行程列表
    this.resetTitle('行程概要')
    axios.post('/api/activity/itinerary', {id: this.$route.params.id}).then(res => {
      this.active = res.list
      this.formcity = res.formcity
    })
    // 商品规格信息
    axios.post('/api/activity/format', {id: this.$route.params.id}).then(res => {
      res.forEach(element => {
        element.number = 0
      })
      this.format = res
    })
  },
  watch: {
    format: {
      handler () {
        let total = 0
        this.format.forEach(item => {
          total += item.price * item.number
        })
        this.price = total.toString()
      },
      deep: true
    }
  },
  methods: {
    order () {
      if (this.popupVisible) {
        console.log('预定席位确认')
        let number = 0
        this.format.forEach(item => {
          number += item.number
        })
        let orderInfo = {
          number: number,
          price: this.price
        }
        if (number > 0) {
          var params = []
          this.format.forEach(item => {
            params.push({
              formatId: item.format_id,
              quantity: item.number
            })
          })
          // 创建订单
          axios.postJson('/api/order/create', {orderInfoDetails: params, activityId: this.$route.params.id}).then(res => {
            console.log('创建订单成功')
            console.log(res)
            if (res.success) {
              orderInfo.orderId = res.orderId
              setOrderInfo(JSON.stringify(orderInfo))
              // 缓存 notice
              // window.location.href = `${window.location.origin}${window.location.pathname}?orderId=${res.orderId}&activeId=${this.$route.params.id}#/order`
              window.location.href = `${process.env.SHARE_ORIGIN}/#/order/${this.$route.params.id}`
              // router.push({name: 'Order', params: {activeId: this.$route.params.id}})
            }
          })
          // router.push({name: 'Order', id: this.$route.params.id})
        } else {
          this.popupVisible = false
          Toast('请选择预定席位')
        }
      } else {
        this.popupVisible = true
      }
    },
    count (item, step) {
      let number = item.number + step
      if (number < 0) {
        return
      }
      if (number <= item.stock) {
        this.$set(item, 'number', number)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.course{
  .mint-header.is-fixed{
    background: #fff;
    color:#2a2a2a;
    height:.7rem;
    font-size:.32rem;
  }
  .itinerary {
    background:#fff;
    font-size:.24rem;
    margin-top: .94rem;
    margin-bottom: 1rem;
    padding: .16rem .4rem;
    .item {
      display: flex;
      padding-bottom: .34rem;
      border-left: 1px solid #f5d00c;
    }
    img {
      width: .4rem;
      height: .4rem;
      // margin-left:
      transform: translateX(-50%);
    }
    .time {
      margin: 0 .1rem;
      width:.9rem;
      color:#ff9800;
    }
    .title {
      font-size:.28rem;
      line-height: .4rem;
    }
    .info {
      color: #8f8f8f;
      line-height: .34rem;
      overflow: hidden;
      * {
        max-width: 5.3rem;
      }
    }
    .detail {
      width: 5.3rem;
    }
  }
  .popup-box {
    width: 7.5rem;
    font-size: .28rem;
    background: #fff;
    padding-bottom: 1.1rem;
    .title{
      height:.94rem;
      line-height: .94rem;
      padding-left:.38rem;
      border-bottom:1px solid #bbbbbb;
    }
    .format-box{
      margin-left:.38rem;
      padding-right:.32rem;
      min-height: .96rem;
      padding-top: .08rem;
      // height: 1.12rem;
      border-bottom:1px solid #bbbbbb;
      .format{
        display: flex;
        justify-content:space-between;
        align-items:center;
        height: 100%;
        .rmb{
          font-size:.24rem;
        }
      }
      .name{
        width:5.05rem;
        .line-clamp{
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
        }
      }
      .info{
        color:#bbbbbb;
        font-size:.24rem;
      }
      .number{
        width:1.75rem;
        display: flex;
        justify-content:space-between;
        span {
          font-size:.32rem;
          min-width:.6rem;
          height: .6rem;
          line-height: .6rem;
          text-align: center;
        }
      }
    }
  }
}
</style>



