import axios from '@/utils/axios'
let wx = window.wx
let configData

export function getToken () {
  // return 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..0-evpZ2z10sTTt3sS0OB4_DvJstg1Vv9aZA2UWWoTXc'
  return window.localStorage.getItem('yz-Token')
}

export function setToken (value) {
  return window.localStorage.setItem('yz-Token', value)
}

export function removeToken (value) {
  return window.localStorage.removeItem('yz-Token')
}

export function removeStorage () {
  return new Promise((resolve, reject) => {
    window.localStorage.removeItem('yz-Token')
    window.localStorage.removeItem('yz-OrderInfo')
    window.localStorage.removeItem('yz-Config')
    resolve()
  })
}

export function getIndexTab () {
  return window.localStorage.getItem('yz-IndexTab')
}

export function setIndexTab (value) {
  return window.localStorage.setItem('yz-IndexTab', value)
}

export function getShopId () {
  return window.sessionStorage.getItem('yz-ShopId')
}

export function setShopId (value) {
  return window.sessionStorage.setItem('yz-ShopId', value)
}

export function getOrderInfo () {
  return window.localStorage.getItem('yz-OrderInfo')
}

export function setOrderInfo (value) {
  return window.localStorage.setItem('yz-OrderInfo', value)
}

export function getConfig () {
  return window.sessionStorage.getItem('yz-Config')
}

export function setConfig (value) {
  return window.sessionStorage.setItem('yz-Config', value)
}

export function wxConfig () {
  let config = (data, resolve, reject) => {
    wx.config({
      debug: data.debug, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
      appId: data.appId, // 必填c，公众号的唯一标识
      timestamp: data.timestamp, // 必填，生成签名的时间戳
      nonceStr: data.nonceStr, // 必填，生成签名的随机串
      signature: data.signature, // 必填，签名，见附录1
      jsApiList: data.jsApiList // ['chooseWXPay'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
    })
    wx.ready(() => {
      // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
      // wx.hideOptionMenu()
      // wx.hideMenuItems({
      //   menuList: ['menuItem:share:qq', 'menuItem:share:weiboApp', 'menuItem:share:facebook', 'menuItem:share:QZone'] // 要隐藏的菜单项，只能隐藏“传播类”和“保护类”按钮，所有menu项见附录3
      // })
      resolve()
      setConfig(true)
    })
    wx.error((res) => {
      // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
      reject(new Error(res.errMsg))
    })
  }
  return new Promise((resolve, reject) => {
    if (!configData) {
      // 'http://www.yuanzigo.com/wx/index.html'
      axios.post('/api/wechat/jssign', {url: window.location.href.split('#')[0], debug: process.env.NODE_ENV === 'production' ? 0 : 1}).then(res => {
        console.log('====wxconfig=====')
        configData = res
        config(res, resolve, reject)
      })
    } else {
      config(configData, resolve, reject)
    }
  })
}

export function wxShare (data, callback) {
  // alert('分享配置')
  // alert(JSON.stringify(data))
  wxConfig().then(result => {
    let shareData = {
      title: data.title, // 分享标题
      link: data.link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
      desc: data.desc, // 分享描述
      imgUrl: data.imgUrl // 分享图标
    }
    if (callback) {
      shareData.success = callback
      shareData.cancel = callback
      shareData.fail = callback
    }
    // 分享到朋友圈
    wx.onMenuShareTimeline(shareData)
    // 分享给朋友
    wx.onMenuShareAppMessage(shareData)
  })
}

export function wxHideMenu () {
  wx.hideOptionMenu()
}

export function wxShowMenu () {
  wx.showMenuItems({
    menuList: ['menuItem:share:appMessage', 'menuItem:share:timeline'] // 要显示的菜单项，所有menu项见附录3
  })
}

export function wxPay (data, orderId) {
  console.log('=====wxpay=====')
  console.log(data)
  return new Promise((resolve, reject) => {
    wxConfig().then(result => {
      wx.chooseWXPay({
        timestamp: data.timestamp || data.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
        nonceStr: data.nonceStr, // 支付签名随机串，不长于 32 位
        package: data.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
        signType: data.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
        paySign: data.paySign, // 支付签名
        success: (res) => {
          // 支付成功后的回调函数
          console.log('=====支付成功====')
          // alert('=====支付成功====')
          // alert('订单id:' + orderId)
          resolve(true)
        },
        fail: (res) => {
          // alert('=====支付失败====')
          // alert(JSON.stringify(res))
          reject(new Error())
        },
        cancel: res => {
          // alert('=====支付取消====')
          // alert(JSON.stringify(res))290-9999
          reject(new Error())
        }
      })
    })
  })
}

export function wxPreviewImage (src, srcList) {
  wx.previewImage({
    current: src, // 当前显示图片的http链接
    urls: srcList // 需要预览的图片http链接列表
  })
}

// 获取url参数
export function getAllUrlParam () {
  // get query string from url (optional) or window
  var url = window.location.url
  var queryString = url ? url.split('?')[1] : window.location.search.slice(1)
  // we'll store the parameters here
  var obj = {}
  // if query string exists
  if (queryString) {
    // stuff after # is not part of query string, so get rid of it
    queryString = queryString.split('/#/')[0]
    // split our query string into its component parts
    var arr = queryString.split('&')
    for (var i = 0; i < arr.length; i++) {
      // separate the keys and the values
      var a = arr[i].split('=')
      // in case params look like: list[]=thing1&list[]=thing2
      var paramNum
      var paramName = a[0].replace(/\[\d*\]/, function (v) {
        paramNum = v.slice(1, -1)
        return ''
      })
      // set parameter value (use 'true' if empty)
      var paramValue = typeof (a[1]) === 'undefined' ? true : a[1]

      // (optional) keep case consistent
      // paramName = paramName // .toLowerCase()
      // paramValue = paramValue // .toLowerCase()

      // if parameter name already exists
      if (obj[paramName]) {
        // convert value to array (if still string)
        if (typeof obj[paramName] === 'string') {
          obj[paramName] = [obj[paramName]]
        }
        // if no array index number specified...
        if (typeof paramNum === 'undefined') {
          // put the value on the end of the array
          obj[paramName].push(paramValue)
        } else {
          // if array index number specified...
          // put the value at that index number
          obj[paramName][paramNum] = paramValue
        }
      } else {
        // if param name doesn't exist yet, set it
        obj[paramName] = paramValue
      }
    }
  }
  return obj
}



