<template>
  <div class="video">
    <v-touch v-on:swipeup="swipeDown">
      <div class="video-box" >
        <video ref="video" :poster="active.videoimg" x5-video-player-type="h5" preload="auto"  :src="active.video">
        </video>
        <img v-show="!play" @click="imageClick()" class="play" src="../assets/img/bofanganniu@3x.png" alt="播放">
        <img src="../assets/img/swipe-down@3x.png" alt="下拉" class="down">
      </div>
    </v-touch>
    <mt-popup ref="popup"
      v-model="popupVisible"
      position="bottom"
      :closeOnClickModal="false"
      >
      <div class="popup-box" ref="popupBox">
        <div class="detail">
          <div class="empty"></div>
          <img v-for="(img, index) in active.intro" :key="index" :src="img" alt="详情图" @click="previewImage(img)">
        </div>
        <img src="../assets/img/arrow-up@3x.png" alt="返回视频" class="up" @click="backVedio()">
        <!-- <img class="detail"  :src="active.intro[0]" alt="详情图" > -->
        <div class="price" v-bind:class="{overstock:active.overstock<=0}">
          <div class="right" @click="order()">
              <!-- {{active.overstock > 0 && '马上预定' || '售罄'}} -->
              行程概要
          </div>
          <div class="left">
            <!-- 每席 <span class="rmb">¥ {{active.price | money}} </span> 起 -->
            <router-link class="shop"  :to="{name:'Index', params:{tab:'tab-out'}}">
              店铺首页
            </router-link>
          </div>

        </div>
      </div>
    </mt-popup>
    <div v-show="!play" class="price" v-bind:class="{overstock:active.overstock<=0}">
      <div class="right" @click="order()">
          <!-- {{active.overstock > 0 && '马上预定' || '售罄'}} -->
          行程概要
      </div>
      <div class="left">
        <!-- 每席 <span class="rmb">¥ {{active.price | money}} </span> 起 -->
        <router-link class="shop"  :to="{name:'Index', params:{tab:'tab-out'}}">
          店铺首页
        </router-link>
      </div>

    </div>
  </div>
</template>

<script>
import axios from '@/utils/axios'
import router from '@/router'
import { wxShare, setShopId, wxPreviewImage } from '@/utils/auth'
export default {
  name: 'Video',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      active: '',
      play: false,
      isInited: false,
      popupVisible: false,
      route: {
        name: 'Course',
        params: {id: this.$route.params.id}
      }
    }
  },
  created () {
    axios.post('/api/activity/detail', {id: this.$route.params.id}).then(res => {
      this.active = res
      this.resetTitle(this.active.title)
      setShopId(this.active.shopid)
      wxShare({
        title: this.active.title,
        link: `${process.env.SHARE_ORIGIN}/#/video/${this.active.id}`,
        imgUrl: this.active.headimg,
        desc: `精品活动也可以不贵。`
      })
      // this.active.overstock = 0
      // this.active.video = 'https://res.ugogo.net/Fg-fe7jFsoWpKJh5JqLu9gULnMUz'
    })
  },
  methods: {
    imageClick () {
      this.play = true
      this.$refs.video.play()
      if (!this.isInited) {
        this.$refs.video.onended = event => {
          this.play = false
        }
        this.$refs.video.onpause = event => {
          this.play = false
        }
        this.isInited = true
      }
    },
    order () {
      if (this.active.overstock > 0) {
        router.push(this.route)
      }
    },
    swipeDown () {
      console.log('swipeUp')
      this.popupVisible = true
    },
    backVedio () {
      this.popupVisible = false
    },
    previewImage (src) {
      wxPreviewImage(src, this.active.intro)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .video .video-box {
    position: fixed;
    top:0;bottom:0;
    left: 0;right:0;
    display: flex;
    background: #666;
    align-items:center;
    justify-content:center;
    video {
      max-width: 100%;
      max-height: 100%;
      width: 100%;
      height: 100%;
      object-fit:fill;
    }
    .play {
      position: fixed;
      width: 2.2rem;
    }
    .down {
      width: .47rem;
      position: fixed;
      bottom: 1.27rem;
    }
    .overstock {
      background: #666;
    }
  }
  .popup-box {
    width: 7.5rem;
    font-size: .28rem;
    background: #fff;
    line-height: 0;
    position: relative;
    .empty {
      height: .46rem;
    }
    .up {
      width: .36rem;
      position: fixed;
      top: 1rem;
      right: .3rem;
    }
    .detail{
      height: calc(100vh - .64rem);
      padding-bottom: 1.1rem;
      overflow-y:scroll;
      -webkit-overflow-scrolling: touch;
      img{
        width:100%;
      }
      margin:0;
    }
  }
</style>



