<template>
  <div class="order">
    <div class="item title">出行人信息（选填）</div>
    <div class="item" v-for="(item, index) in traveler" :key="index">
      <mt-field label="姓名" v-model="item.name.value" :state="item.name.state"  placeholder="请输入姓名"></mt-field>
      <mt-field label="联系手机" type="number" v-model="item.tel.value" :state="item.tel.state"  placeholder="请输入中国大陆手机号"></mt-field>
      <mt-field label="身份证"  v-model="item.idcard.value" :state="item.idcard.state" placeholder="请输入身份证号码"></mt-field>
    </div>
    <div class="item btn" v-show="traveler.length < orderInfo.number">
      <mt-button  type="primary" size="large" @click="add()">添加出行人</mt-button>
    </div>

    <div class="item title concat">
      联系人信息
    </div>
    <div class="item">
      <mt-field label="姓名" v-model="contacter.name.value" :state="contacter.name.state"  placeholder="请输入姓名"></mt-field>
      <mt-field label="联系手机" type="number" v-model="contacter.tel.value" :state="contacter.tel.state" placeholder="请输入中国大陆手机号"></mt-field>
      <mt-field label="联系邮箱" v-model="contacter.email.value" :state="contacter.email.value && contacter.email.state" placeholder="请输入联系邮箱（选填）"></mt-field>
    </div>

    <div class="item attaintion">
      <img src="../assets/img/zhuyishixiang@3x.png" alt="注意事项">
      <div class="info" v-html="notice">
        <!-- <div class="title">注意事项</div>
        <p>1、游客入住酒店后如需升级或者更换房型，需补交升级或者更换的房差费用。</p>
        <p>2、本产品门票为团队优惠联票，持老人证，学生证等优惠证件的人群不再享受门票优惠。敬请谅解</p>
        <div v-html="notice"></div> -->
      </div>
    </div>

    <div class="price" ref="price">
      <div class="right" @click="pay()">
        支付
      </div>
      <div class="left">
        金额 <span class="rmb">¥ {{orderInfo.price | money}} </span>
      </div>
    </div>
  </div>
</template>

<script>
import { getOrderInfo, wxPay } from '@/utils/auth'
import { Toast } from 'mint-ui'
import axios from '@/utils/axios'
import router from '@/router'
export default {
  name: 'Order',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      params: {},
      orderInfo: {
        price: 0,
        number: 0
      },
      traveler: [
        {
          name: {
            value: '',
            state: ''
          },
          tel: {
            value: '',
            state: ''
          },
          idcard: {
            value: '',
            state: ''
          }
        }
      ],
      contacter: {
        name: {
          value: '',
          state: ''
        },
        tel: {
          value: '',
          state: ''
        },
        email: {
          value: '',
          state: ''
        }
      },
      notice: `<p>1、游客入住酒店后如需升级或者更换房型，需补交升级或者更换的房差费用。</p>
        <p>2、本产品门票为团队优惠联票，持老人证，学生证等优惠证件的人群不再享受门票优惠。敬请谅解</p>`
    }
  },
  created () {
    // alert(window.location.href)
    let ua = window.navigator.userAgent.toLowerCase()
    if (/iphone|ipad|ipod/.test(ua) && !window.sessionStorage.getItem('yz-ios-reload')) {
      window.sessionStorage.setItem('yz-ios-reload', true)
      window.location.reload()
    }
    this.resetTitle('订单确认')
    this.orderInfo = JSON.parse(getOrderInfo())
    if (!this.orderInfo.number) {
      Toast('订单信息丢失')
      router.push({name: 'Index'})
    }
    // this.params = getAllUrlParam()
    console.log('======order=====')
    // console.log(this.params)
    // 获取注意事项
    axios.get(`/api/activity/getNotice?id=${this.$route.params.activeId}`).then(res => {
      console.log(res)
      this.notice = res.notice
    })
  },
  methods: {
    pay () {
      let result = true
      let traveler = []
      this.traveler.forEach(item => {
        if ((item.name.value || item.tel.value || item.idcard.value)) {
          if (!this.verify(item, false)) {
            result = false
          } else {
            console.log('yige出行人')
            traveler.push({
              name: item.name.value,
              tel: item.tel.value,
              idcard: item.idcard.value
            })
          }
        }
      })
      if (!result) {
        return Toast('出行人信息填写有误')
      }
      // if (this.traveler.length < this.orderInfo.number) {
      //   return Toast('出行人数有误')
      // }
      if (!this.verify(this.contacter)) {
        return Toast('联系人信息有误')
      }
      let contact = {
        name: this.contacter.name.value,
        tel: this.contacter.tel.value,
        email: this.contacter.email.value
      }
      // 调起支付
      let params = {orderId: this.orderInfo.orderId, traveler: traveler, contact: contact}
      axios.postJson('/api/payment/prepay', params).then(res => {
        console.log('===prepay===')
        console.log(res)
        // Toast('调起支付')
        wxPay(res, this.orderInfo.orderId).then(result => {
          router.push({name: 'AdterPay', params: {result: 1}})
        }).catch(result => {
          // 支付失败
          router.push({name: 'AdterPay', params: {result: 0}})
        })
      })
    },
    isValid (value, reg, obj) {
      if (value.toString().match(reg)) {
        this.$set(obj, 'state', 'success')
        return true
      } else {
        this.$set(obj, 'state', 'error')
        return false
      }
    },
    isEmpty (value, obj, notRequire) {
      if (value) {
        this.$set(obj, 'state', 'success')
        return true
      } else {
        if (!notRequire) {
          this.$set(obj, 'state', 'error')
        }
        return false
      }
    },
    add () {
      if (this.traveler.length < this.orderInfo.number) {
        let newPeople = {
          name: {
            value: '',
            state: ''
          },
          tel: {
            value: '',
            state: ''
          },
          idcard: {
            value: '',
            state: ''
          }
        }
        this.traveler.push(newPeople)
      }
      console.log(this.traveler)
    },
    verify (obj) {
      let result = true
      let reg = '^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$'
      if (!this.isEmpty(obj.name.value, obj.name)) {
        result = false
      }
      if (!this.isEmpty(obj.tel.value, obj.tel) || !this.isValid(obj.tel.value, reg, obj.tel)) {
        result = false
      }
      reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
      if (obj.idcard && (!this.isEmpty(obj.idcard.value, obj.idcard) || !this.isValid(obj.idcard.value, reg, obj.idcard))) {
        result = false
      }
      reg = '^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$'
      if (obj.email && this.isEmpty(obj.email.value, obj.email, true) && !this.isValid(obj.email.value, reg, obj.email)) {
        result = false
      }
      return result
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.order{
  margin-bottom: 1rem;
  .item{
    margin-bottom: .24rem;
    background: #fff;
    &.concat{
      margin: 0;
      border-bottom: 1px solid #bbbbbb;
    }
    &.attaintion{
      display: flex;
      padding:.26rem .3rem;
      font-size:.24rem;
      img{
        width: .9rem;
        height: .9rem;
      }
      .info{
        margin-left: .2rem;
        padding-left:.3rem;
        .title {
          height: .46rem;
          line-height: .46rem;
          margin-bottom: .14rem;
        }
        p{
          padding-left: .3rem;
          margin: 0;
          padding-bottom: .2rem;
          color: #969696;
        }
      }
    }
  }
  .btn{
    padding:.24rem .3rem;
  }
  .title{
    padding-left: .3rem;
    background: #fff;
    color:#2a2a2a;
    height:.8rem;
    font-size:.28rem;
    line-height: .8rem;
    text-align: left;
  }
}
</style>



