<template>
  <div class="index">
    <mt-tab-container v-model="tabValue">
        <!-- 现在出发 -->
        <mt-tab-container-item id="tab-out">
            <router-link v-for="hot in hots" :key="hot.id" :to="{name:'Video', params:{id:hot.id}}">
              <div class="bg">
                <img :src="hot.headimg" alt="首页占位图">
                <div class="text">{{hot.title}}</div>
              </div>
            </router-link>
            <mt-navbar v-model="navValue">
                <mt-tab-item id="all">
                  <span class="title">大家都在看</span>
                </mt-tab-item>
                <mt-tab-item id="hot">
                  <span class="title">
                    24小时热卖
                  </span>
                </mt-tab-item>
            </mt-navbar>
            <mt-tab-container v-model="navValue">
                <mt-tab-container-item id="all">
                  <div class="swiper-contain">
                    <swiper :options="swiperOption">
                        <swiper-slide v-for="slide in look" :key="slide.title">
                          <router-link v-if="slide" :to="{name:'Video', params:{id:slide.id}}">
                            <div class="img-box">
                              <img :src="slide.headimg" alt="">
                            <div class="text">{{slide.title}}</div>
                          </div>
                          </router-link>
                        </swiper-slide>
                    </swiper>
                  </div>
                </mt-tab-container-item>
                <mt-tab-container-item id="hot">
                    <div class="swiper-contain">
                    <swiper :options="swiperOption">
                        <swiper-slide v-for="slide in order" :key="slide.title">
                          <router-link v-if="slide" :to="{name:'Video', params:{id:slide.id}}">
                            <div class="img-box">
                              <img :src="slide.headimg" alt="">
                            <div class="text">{{slide.title}}</div>
                          </div>
                          </router-link>
                        </swiper-slide>
                    </swiper>
                  </div>
                </mt-tab-container-item>
            </mt-tab-container>
        </mt-tab-container-item>
        <!-- 我的订单 -->
        <mt-tab-container-item id="tab-order">
            <!-- 订单 -->
            <div class="order-list">
              <div class="order-item" v-for="item in myOrder" :key="item.ordercode">
                <div class="order-header">
                  <span>订单编号：{{item.ordercode}}</span>
                  <span class="status active">
                    {{item.status}}
                  </span>
                </div>
                <div class="order-body">
                  <router-link :to="{name:'Video', params:{id:item.activity_id}}">
                    <img :src="item.headimg" alt="订单缩略图">
                  </router-link>
                  <div class="title">
                    {{item.subtitle}}
                    <br>
                    <span class="gray" v-for="format in item.orderDetails" :key="format">
                      {{format.quantity}}{{format.name}}
                    </span>
                  </div>
                  <div class="price yellow">
                    ￥{{item.price | money}}
                  </div>
                </div>
                <div class="order-footer">
                  <mt-button size="small" v-if="item.status == '待支付'" class="btn-default full" @click="rePay(item.orderid)">付款 {{item.payLimitTime | time}} </mt-button>
                  <mt-button size="small" v-if="item.status == '已支付'" class="btn-default out-line" @click="sure(item.orderid)">确认出行</mt-button>
                  <!-- <mt-button size="small" class="out-line">活动回顾</mt-button> -->
                  <router-link v-if="item.status == '已确认'" :to="{name:'Video', params:{id:item.activity_id}}">
                    <mt-button size="small"  class="btn-default out-line">再玩一次</mt-button>
                  </router-link>
                  <!-- <mt-button size="small">评价有礼</mt-button> -->
                </div>
              </div>
            </div>
        </mt-tab-container-item>
    </mt-tab-container>
    <mt-tabbar v-model="tabValue">
        <mt-tab-item id="tab-out">
          <img v-if="tabValue == 'tab-out'" slot="icon" src="../assets/img/faxian_active@3x.png">
          <img v-else  slot="icon" src="../assets/img/faxian@3x.png" alt="">
          发现
        </mt-tab-item>
        <mt-tab-item id="tab-order">
          <img v-if="tabValue == 'tab-order'" slot="icon" src="../assets/img/dingdan_active@3x.png">
          <img v-else  slot="icon" src="../assets/img/dingdan@3x.png" alt="">
          <!-- <img slot="icon" src="../assets/100x100.png"> -->
          订单
        </mt-tab-item>
    </mt-tabbar>
  </div>
</template>
<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import axios from '@/utils/axios'
import { setInterval, clearInterval } from 'timers'
import { wxPay, setOrderInfo, wxShowMenu, wxHideMenu, getIndexTab, setIndexTab, wxShare, getShopId } from '@/utils/auth'
// import { Toast } from 'mint-ui'
import router from '@/router'
export default {
  name: `Index`,
  components: {
    swiper,
    swiperSlide
  },
  data () {
    return {
      msg: `Welcome to Your Vue.js App`,
      tabValue: `tab-out`,
      navValue: `all`,
      swiperOption: {
        slidesPerView: 3
      },
      hots: [],
      look: [],
      order: [],
      myOrder: []
    }
  },
  created () {
    // let ua = window.navigator.userAgent.toLowerCase()
    this.resetTitle('原子严选')
    if (this.$route.params.tab) {
      this.tabValue = this.$route.params.tab
    } else if (getIndexTab()) {
      this.tabValue = getIndexTab()
    }
    if (!window.sessionStorage.getItem('yz-ios-reload')) {
      // window.location.reload()
      window.sessionStorage.setItem('yz-ios-reload', true)
      window.location.href = `${process.env.SHARE_ORIGIN}/#/`
    }
    let taskArr = [
      axios.get('/api/activity/getlist', { params: { hot: 1, shopid: getShopId() } }),
      axios.get('/api/activity/getlist', { params: { hot: 0, order: 'look', shopid: getShopId() } }),
      axios.get('/api/activity/getlist', { params: { hot: 0, order: 'order', shopid: getShopId() } })
    ]
    axios.requestAll(taskArr).then(resArr => {
      this.hots = (resArr[0] && resArr[0].list) || []
      this.look = resArr[1].list
      this.order = resArr[2].list
      this.resetTitle(resArr[0].shopname)
      if (this.hots.length) {
        wxShare({
          title: resArr[0].shopname,
          link: `${process.env.SHARE_ORIGIN}/?shopid=${getShopId()}#/index/tab-out`,
          imgUrl: this.hots[0].headimg,
          desc: `${this.hots[0].title}正在热卖，马上来抢吧。`
        })
      }
    })
    // .catch(res => {
    //   Toast('服务器开小差咯')
    // })
    // 分开写，myorder接口报错的时候首页仍然有数据
    this.orderInit()
  },
  watch: {
    tabValue: (value, oldValue) => {
      if (value !== oldValue) {
        if (value === 'tab-out') {
          wxShowMenu()
          // this.resetTitle('原子严选 ● 烟花三月')
        } else {
          wxHideMenu()
          // this.resetTitle('我的订单')
        }
        setIndexTab(value)
      }
    }
  },
  methods: {
    orderInit () {
      axios.get('/api/order/mylist', {params: {shopid: getShopId()}}).then(res => {
        this.myOrder = res
        this.myOrder.forEach((item, index) => {
          // this.$set(item, 'payLimitTime', 0) //
          if (item.payLimitTime <= 0 && item.status === '待支付') {
            // item.status = '已过期'
            this.$set(item, 'status', '已过期')
          }
          let inter = setInterval(() => {
            if (item.payLimitTime > 0 && item.status === '待支付') {
              this.$set(item, 'payLimitTime', --item.payLimitTime)
            } else if (item.status === '待支付') {
              clearInterval(inter)
              this.$set(item, 'status', '已过期')
            }
          }, 1000)
        })
      })
    },
    rePay (orderId) {
      // console.log('orderId', orderId)
      setOrderInfo(JSON.stringify({orderId: orderId}))
      axios.postJson('/api/payment/prepay', {orderId: orderId}).then(res => {
        wxPay(res, orderId).then(result => {
          // 支付成功
          router.push({name: 'AdterPay', params: {result: 1, orderId: orderId}})
        }).catch(result => {
          // 支付失败
          router.push({name: 'AdterPay', params: {result: 0, orderId: orderId}})
        })
      })
    },
    sure (orderId) {
      axios.post('/api/order/confirmTravel', {orderId: orderId}).then(res => {
        console.log(res)
        // Toast('')
        this.orderInit()
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
/* navbar 样式修改  */
.mint-navbar {
  border-bottom: 2px solid #bbbbbb;
  height: .8rem;
  .mint-tab-item {
    padding: 0;
    .title {
      display: inline-block;
      height: .8rem;
      line-height: .8rem;
      padding: 0 0.1rem;
    }
  }
  .mint-tab-item.is-selected {
    border: none;
    color: #ff9800;
    .title {
      border-bottom: 4px solid #ff9800;
    }
  }
}
/* tabbar 样式修改 */
.mint-tabbar {
  position: fixed;
  background: #fff;
  z-index: 9;
  .mint-tab-item {
    .mint-tab-item-label {
      font-size: 0.24rem;
      color: red;
    }
    &.is-selected {
      background: transparent !important;
      color: #ff9800;
    }
  }
}
.index {
  padding-bottom: 1.1rem;
  img {
    width: 100%;
    display: block;
  }
  .bg {
    position: relative;
    color: #fff;
    margin-bottom: 0.12rem;
    .text {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 3.58rem;
      font-size: 0.56rem;
      text-align: center;
      border-bottom: 2px solid #fff;
    }
  }
  .swiper-contain {
    padding: 0.08rem 0;
    background: #fff;
    .img-box {
      position: relative;
      padding: 0 0.15rem;
      font-size: 0.28rem;
      .text {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: #fff;
      }
    }
  }
  .order-list {
    .order-item {
      margin-bottom: 0.24rem;
      background: #fff;
      .order-header,
      .order-body,
      .order-footer {
        display: flex;
        padding: 0.1rem 0.36rem;
        font-size: 0.28rem;
        justify-content:flex-end;
      }
      .order-header,
      .order-body {
        border-bottom: 1px solid #bbbbbb;
        justify-content: space-between;
      }
      .order-header .status {
        float: right;
        &.active {
          color: #ff9800;
        }
      }
      .order-body {
        align-items: center;
        height: 1.27rem;
        img {
          width: 1.2rem;
          height: 1.2rem;
        }
        .title {
          flex: 1;
          padding-left: 0.2rem;
        }
        .price {
          position: relative;
          width: auto;
          border: none;
          height: auto;
          color: #ff9800;
        }
        .gray {
          color:#969696;
          font-size:.24rem;
          margin-right: .1rem;
        }
      }
      .order-footer {
        height: 0.7rem;
        align-items: center;
        padding: 0.1rem 0.3rem;
        button {
          padding: 0;
          width: 1.6rem;
          height: 0.6rem;
        }
      }
    }
  }
}
</style>



