// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import MintUI from 'mint-ui'
// import VueGesture from 'vue-gesture'
import VueTouch from 'vue-touch'
import 'mint-ui/lib/style.css'
import './assets/sass/common.scss'
import App from './App'
import router from './router'
import Price from '../src/components/common/price'

Vue.config.productionTip = false

// VueGesture.config = {
//   maxSingleTapTimeInterval: 200
// }

Vue.use(MintUI)
Vue.use(VueTouch, {name: 'v-touch'})
Vue.component(Price.name, Price)
Vue.filter('money', value => {
  if (!value) return
  value = value.toString()
  return value.replace(/^(-?)(\d+)((\.\d+)?)$/, (s, s1, s2, s3) => {
    let rex = /\d{1,3}(?=(\d{3})+$)/g
    let result = s1 + s2.replace(rex, '$&,') + s3
    return result
  })
})
Vue.filter('date', (input) => {
  let d = new Date(input)
  let year = d.getFullYear()
  let month = d.getMonth() + 1
  let day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate()
  // let hour = d.getHours()
  // let minutes = d.getMinutes()
  // let seconds = d.getSeconds()
  return year + '年' + month + '月' + day + '日' // + hour + ':' + minutes + ':' + seconds
})

Vue.filter('time', (input) => {
  return parseInt(input / 60) + ':' + input % 60
})

// IOS端title重定义
Vue.prototype.resetTitle = title => {
  document.title = title
  let ua = navigator.userAgent
  if (/\bMicroMessenger\/(d\.+)/.test(ua) && /ip(hone|od|ad)/i.test(ua)) {
    var i = document.createElement('iframe')
    i.src = ''
    i.style.display = 'none'
    i.onload = function () {
      setTimeout(function () {
        i.remove()
      }, 9)
    }
    document.body.appendChild(i)
  }
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



