<template>
  <div class="hello">
    <!-- <h1>{{ msg }}</h1> -->
    <mt-button size="small" @click="clear()">清除缓存 </mt-button>
    <div>
      当前token：<br>
      {{token}}
    </div>
  </div>
</template>

<script>
import { getToken, removeStorage } from '@/utils/auth'
// import { Toast } from 'mint-ui'
// import router from '@/router'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      token: ''
    }
  },
  created () {
    this.token = getToken()
  },
  methods: {
    clear () {
      removeStorage().then(result => {
        alert('token 已清除，请先关闭页面再打开')
      })
      // router.push({name: 'Index'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
</style>



